























import axios from "axios";

export default {
  name: "login",

  data() {
    return {
      usernameConfirm: true,
      passwordConfirm: true,
      email: null,
      password: null,
    };
  },
  methods: {
    Login(e: Event) {
      e.preventDefault();
      console.log("done");
      this.usernameConfirm = true;
      this.passwordConfirm = true;
      console.log(this.email);

      if (!this.email) {
        this.usernameConfirm = false;
      }
      if (!this.password) {
        this.passwordConfirm = false;
      }
      let data = {
        email:this.email,
        password: this.password
      };

      axios
        .post("", data)
        .then((res) => {
          console.log(res);
        })
        .catch((err) => {
          console.log(err);
        });

    },
  },
};
